import React, { Fragment } from 'react';
import { map, stylemap } from './../../constant/menu';
import { actSendRequest } from './../../action/action';
import { connect } from 'react-redux';

class contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            txtTen: '',
            txtEmail: '',
            txtSdt: '',
            txtComment: '',
            form: true,
        }
    }

    onChange = e => {
        var name = e.target.name;
        var value = e.target.value;
        this.setState({
            [name]: value
        });
    }

    onSubmit = e => {
        e.preventDefault();
        this.props.actSendRequest({
            hovaten: this.state.txtTen,
            email: this.state.email,
            dt: this.state.txtSdt,
            comment: this.state.txtComment
        });
        this.setState({
            form: false
        });
    }

    render() {
        var { form } = this.state;
        var item = form === true ? <form onSubmit={(e) => this.onSubmit(e)}>
            <h4 className="text-uppercase">thng tin lin h</h4>
            <div className="input-child">
                <div className="title-input">
                    <label>H v tn</label><small> * :</small>
                </div>
                <input type="text" name="txtTen" onChange={(e) => this.onChange(e)} />
            </div>
            <div className="input-child">
                <div className="title-input">
                    <label>a ch email</label><small> * :</small>
                </div>
                <input type="text" name="txtEmail" onChange={(e) => this.onChange(e)} />
            </div>
            <div className="input-child">
                <div className="title-input">
                    <label>S T</label><small> * :</small>
                </div>
                <input type="text" name="txtSdt" onChange={(e) => this.onChange(e)} />
            </div>
            <div className="input-child">
                <div className="title-input">
                    <label>Bnh lun</label><small> * :</small>
                </div>
                <textarea className="form-control" name="txtComment" onChange={(e) => this.onChange(e)} ></textarea>
            </div>
            <input type="submit" className="btn btn-primary" />
        </form>:<span className="label label-success">Cm n bn  gi phn hi</span>;

        return (
            <Fragment>
                <main>
                    <div className="path"><span>Home / </span><span>Lin h</span></div>
                    <div className="map--contact">
                        <iframe src={map} style={stylemap} title="this is map" ></iframe>
                    </div>
                    <div className="content--contact">
                        <div className="content--contact-input">
                            {item}
                        </div>
                        <div className="content--contact-info">
                            <figure className="position-relative"><img src={require('./../../assets/images/Green Shop/gs.png')} alt="green shop" /><span className="position-absolute">Mn qu t thin nhin</span></figure>
                            <p>DKT c thnh lp vi nim am m v kht vng thnh cng trong lnh vc Thng mi in t.Chng ti  v ang khng nh c v tr hng u bng nhng sn phm</p>
                            <div className="phone-contact"><img src={require('./../../assets/images/Green Shop/phone.png')} alt="in thoi" /><span>in thoi : (84 -4) 66.558.868 </span></div>
                            <div className="email-contact"><img src={require('./../../assets/images/Green Shop/email.png')} alt="mail" /><span>Email : info@dkt.com.vn</span></div>
                        </div>
                    </div>
                </main>
            </Fragment>
        );
    }

}

export default connect(null, function(dispatch){
    return {
        actSendRequest: request => {
            dispatch(actSendRequest(request));
        }
    }
} )(contact);
