import React, { Component } from 'react';
import { connect } from 'react-redux'
import Button from './../commons/button/Button';
import { Link } from 'react-router-dom';
import { getUser } from '../../action/action';
class Paypal extends Component {

     render() {
        var userLocal = JSON.parse(localStorage.getItem('profileUser'));
        const userLogin = localStorage.getItem('profileUser')
        if (!userLogin) {
            window.location.href = '/login'
        } else {
        
            if (userLocal.statusPay) {
                var totalQuantity = 0
                userLocal.historyPay.map((val) => {
                    totalQuantity += +val.quantity;
                    return true
                }) 
                var totalProduct = ''
                userLocal.historyPay.map((val) => {
                    totalProduct += val.name + '-'
                    return true
        })
            }
        }
        
        return (
            <div class="content-right col-6 bg-white px-5 my-3">
                <h1 class="text-uppercase py-4">Ho n ca bn</h1>
                <p class="py-3"><b>Tn : </b> { userLocal.name }  </p>
                <p class="py-3"><b>S in thoi : </b> { userLocal.phone }</p>
                <p class="py-3"><b>Tng s sn phm  mua : </b> { totalQuantity }</p>
                <p class="py-3"><b>Sn phm  mua : </b> { totalProduct }</p>
                <p class="py-3"><b>Tng tin cn thanh ton : </b> { userLocal.totalPay }</p>
                <p class="py-3"><b>Tnh trng n hng : </b> { userLocal.statusPay == true ? 'Sn phm ang c vn chuyn' : 'Bn cha t hng' }</p>
                <Link to='/'><Button name='Quay v trang ch' /></Link>
            </div>
            );
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        getUser: state.userPaypal
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        getAllUser: () => {
            dispatch(getUser())
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Paypal)