import React, { Fragment } from 'react';
import Button from '../commons/button/Button';
import { connect } from 'react-redux';
import { addUser } from './../../action/action';
const uuidv1 = require('uuid/v1');


class Signup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id : uuidv1(),
            name : '',
            phone : '',
            email : '',
            website : '',
            password : '',
            repassword : ''
        }
    }
    
    changeSignup = (event) => {
        let name = event.target.name;
        let value = event.target.value;
        this.setState({
            [name] : value
        })
    }

    clickSignup = () => {
        const { phone, email, password, repassword } = this.state;
        // validate phone 
        function checkPhone(phone) {
            return /^[(]{0,1}[0-9]{3}[)]{0,1}[-\s]{0,1}[0-9]{3}[-\s]{0,1}[0-9]{4}$/.test(phone)
        }
        let isPhone = checkPhone(phone);
        if(!isPhone) {
            alert('S in thoi khng hp l !')
            return false
        }

        // validate email
        function checkEmail(email) {
            return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)
        }
        let isEmail = checkEmail(email);
        if(!isEmail) {
            alert('Email khng ng')
            return false
        }
        // validate pass
        if(password !== repassword) {
            alert('Mt khu khng khp')
            return false
        }
        this.props.signup(this.state)
        
    }
    render() {
        return (
            <Fragment>

                <main id="signup">
                    <div className="path"><span>Home / </span><span>ng k</span></div>
                    <div className="info--customer">
                        <div className="info--customer-title">
                            <h4 className="text-uppercase">thng tin cc nhn</h4>
                        </div>
                        <div className="info--customer-content d-flex">

                            <div className="left">
                                <div className="input-child">
                                    <div className="title-input">
                                        <label>H v tn</label><small> * :</small>
                                    </div>
                                    <input type="text" name='name' onChange={ (event) => this.changeSignup(event) }/>
                                </div>
                                
                                <div className="input-child">
                                    <div className="title-input">
                                        <label>S T</label><small> * :</small>
                                    </div>
                                    <input type="text" name='phone' onChange={ (event) => this.changeSignup(event) }/>
                                </div>
                                <input type="checkbox" id="signup-email" />
                                <label>ng k nhn thng tin qua email</label>
                            </div>

                            <div className="right">
                                <div className="input-child">
                                    <div className="title-input">
                                        <label>a ch email</label><small> * :</small>
                                    </div>
                                    <input type="text" name='email' onChange={ (event) => this.changeSignup(event) }/>
                                </div>
                                <div className="input-child">
                                    <div className="title-input">
                                        <label>Website ca bn</label><small> * :</small>
                                    </div>
                                    <input type="text" name='website' onChange={ (event) => this.changeSignup(event) }/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="info--customer">
                        <div className="info--customer-title">
                            <h4 className="text-uppercase">thng tin ti khon</h4>
                        </div>
                        <div className="info--customer-content d-flex">
                            <div className="left">
                                <div className="input-child">
                                    <div className="title-input">
                                        <label>Mt khu</label><small> * :</small>
                                    </div>
                                    <input type="text" name='password' onChange={ (event) => this.changeSignup(event) }/>
                                </div>
                            </div>
                            <div className="right">
                                <div className="input-child">
                                    <div className="title-input">
                                        <label>Nhp li mt khu</label><small> * :</small>
                                    </div>
                                    <input type="text" name='repassword' onChange={ (event) => this.changeSignup(event) }/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="btn-signup">
                        <Button name ='quay li' />
                        <button className="text-uppercase" onClick = { () => this.clickSignup() } >ng k</button>
                    </div>
                </main>
            </Fragment>
        );
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        signup: (info) => {
            dispatch(addUser(info))
        }
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        prop: state.prop
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Signup)